<template>
  <div>
    <el-container>
      <el-aside style="width: 200px;min-height: 100vh ;background-color: #001529">
        <div style="height: 60px;color: white;display:flex; align-items: center;justify-content: center">
          求职者控制台        </div>
        <el-menu :default-active=$route.path active-text-color="#fff" background-color="#001529" router
                 style="border: none;background-color: red" text-color="rgba(255,255,255,0.65)">
          <el-menu-item index="/home">
            <template slot="title">
              <i class="el-icon-house"></i>
              <span>系统首页</span>
            </template>
          </el-menu-item>

          <el-menu-item index="/recruit">招聘信息</el-menu-item>
          <el-menu-item index="/interview">面试通知</el-menu-item>

          <el-submenu>
            <template slot="title">
              <i class="el-icon-menu"></i>
              <span>用户信息管理</span>
            </template>
            <el-menu-item index="self">查看用户信息</el-menu-item>
            <el-menu-item index="resume">查看个人简历</el-menu-item>
          </el-submenu>
          <el-menu-item index="/">用户退出</el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header></el-header>
        <el-main>
          <el-table border :data="interview">
            <el-table-column label="姓名" prop="userName"/>
            <el-table-column label="公司" prop="companyName"/>
            <el-table-column label="招聘岗位" prop="recruitmentPosition"/>

            <el-table-column label="面试状态" prop="status"/>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <el-button type="primary" @click="qx(scope.row.id)">取消面试</el-button>
              </template>

            </el-table-column>


          </el-table>
        </el-main>
      </el-container>

    </el-container>


  </div>
</template>
<style>
.el-menu--inline .el-menu-item {
  background-color: #000c17 !important;
}

.el-menu-item:hover, .el-submenu__title:hover {
  color: #fff !important;
}

.el-submenu__title:hover .el-icon-menu {
  color: #fff !important;
}

.el-menu-item.is-active {
  background-color: red !important;
}


