<script>
import request from "@/utils/request";

export default {
  name: 'interview',
  data() {
    return {
      interview: []
    }
  },
  methods: {

    qx(id) {
      request.get('/user/cancelInterview/' + id).then(res => {
        if (res.data.code === 1) {
          this.$message.success("取消成功");
          location.reload()
        } else {
          this.$message.error("取消失败")
        }

      })
    }
  },
  mounted() {
    request.get('/user/interview').then(res => {
      console.log(res.data.data);
      this.interview = res.data.data;

      let length = this.interview.length;
      for (let i = 0; i < length; i++) {
        if (res.data.data[i].status === 0) {
          this.interview[i].status = '未处理'
        }
        if (res.data.data[i].status === 1) {
          this.interview[i].status = '同意'
        }
        if (res.data.data[i].status === 2) {
          this.interview[i].status = '拒绝'
        }
      }
    })
    /*   axios.get(`${config.server}/user/all`).then(
           res=>{
             console.log(res.data.data);
             this.users=res.data.data
           }
       )*/
  }
}
</script>
